import React from "react";
import "./Header.css";
import fistIcon from "../fist.png";

export default function Header() {
  return (
    <div className="Header">
      <div className="header-wrap">
        <img className="fist-icon" src={fistIcon} />
        <div className="title">     </div>
      </div>
    </div>
  );
}
